<template>
    <div>
        <header id="home">
            <!--HEADER BACKGROUND-->
            <div class="header-background section"></div>
        </header>
        <router-link to="/"><div class="back-button"><span><i class="fa fa-angle-double-left"></i></span></div></router-link>
        <div id="v-card-holder" class="section">
            <div class="section-title">
                    <h4 class="text-uppercase text-center"><img src="images/icons/post-it.png" alt="demo">Github Projects</h4>
            </div>
            <div class="container">
                <div class="row">
                    <div class="col-md-12">
                        <!-- DETAILS -->
                        <div id="about-card" class="card">
                            <loading :active.sync="!this.isLoaded"></loading>
                            <div class="card-content">
                                <!-- ABOUT PARAGRAPH -->
                                <div class="calendar">
                                    <!-- Loading stuff -->
                                    Loading Contribution Data
                                </div>
                                <br><br>
                                
                                <div v-if="this.isLoaded" class="panel-group">
                                    
                                    <div class="panel panel-default" v-for="(repo, index) in this.repos" :key="index">
                                        <div class="panel-heading">
                                            <h4 class="panel-title">
                                            <a data-toggle="collapse" :href="'#'+index" @click="fetchData(index)">{{repo.name}}</a>
                                            </h4>
                                        </div>
                                        <div :id="index" class="panel-collapse collapse">
                                            <div class="panel-body">
                                                <p style="text-align:left"><b>Name:</b> {{repo.name}} </p>
                                                <p style="text-align:left"><b>Description:</b> {{repo.description}} </p>
                                                <p style="text-align:left"><b>Forked? :</b> {{repo.fork}} </p>
                                                <p style="text-align:left"><b>Langulage :</b> {{repo.language}} </p>
                                                <p style="text-align:left"><b>URL:</b><a :href="repo.html_url"> {{repo.html_url}}</a> </p>
                                                <div v-if="contrFetched">
                                                    <p style="text-align:left"><b>Contributors:</b></p><br>
                                                    <div class="row">
                                                        <div class="col-md-4" v-for="(contributor, j) in getContribution(index)" :key="j">
                                                            <img width="100" :src="contributor.avatar_url">
                                                            <p style="text-align:left">Name: <a href="https://github.com/pp.kavinda"> {{contributor.login}} </a></p>
                                                            <p style="text-align:left">contributions: {{contributor.contributions}}</p>
                                                        </div>
                                                    </div>
                                                </div>
                                            </div>
                                            <div class="panel-footer">Panel Footer</div>
                                        </div>
                                    </div>
                                </div>
                                
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </div>
</template>

<script>



import { githubAPI } from '../services/githubAPI'
import Loading from 'vue-loading-overlay';
// import login from '../../public/javascript/github.js'

const github = new githubAPI()

export default {
    name: "github",
    data(){
        return {
            repos: [],
            isLoaded: false,
            contribution: [],
            contrFetched: false,
        }
    },
    components: {
        Loading
    },
    methods: {
        getRepos() {
            github.getrepos().then((Data) => {
                Data.forEach(element => {
                    this.repos.push(element)
                });
                this.isLoaded = true
            })
        },
        fetchData(index){
            if(this.contribution[index]==null){
                this.contrFetched = false;
                var clicked = this.repos[index];
                github.getContributions(clicked.name).then((Data) => {
                    this.contribution[index] = [];
                    Data.forEach(element => {
                        this.contribution[index].push(element)
                    })
                    this.contrFetched = true;
                })
            }
        },
        isEmpty(index){
            return this.repos[index]==null;
        },
        getContribution(index){
            return this.contribution[index];
        }
    },
    created(){
        this.getRepos();
        // loadProfile()
        // login.login()
    },
    updated(){
        GitHubCalendar (".calendar", "danushka96", { responsive: true });
    }
}
</script>
