<template>
    <section id="blog" class="section">
        <div class="container">
            <!-- SECTION TITLE -->
            <div class="section-title">
                <h4 class="text-uppercase text-center"><img src="images/icons/post-it.png" alt="demo">Blog</h4>
            </div>
            <div id="blog-card" class="row">
                <div class="col-md-12 col-sm-12">
                    <div class="card">
                        <!--BLOG ODD-->
                        <loading :active.sync="isLoading" :can-cancel="true" ></loading>
                        <div v-for="(post, index) in this.posts" :key="index">
                            <div :class="[isOdd(index) ? 'blog odd' : 'blog even']">
                                <!--IMAGE-->
                                <div class="image" v-if="isLoaded[index]">
                                    <img alt="blog-image" :src="getImage(index)"/>
                                    <div class="image-overlay">
                                        <div class="comments">
                                            <i class="fa fa-user"></i> Danushka Herath {{index}}
                                            <i class="fa fa-clock-o"></i>
                                            <time datetime="2045-08-16">{{post.date}}</time>
                                            <i class="fa fa-comments"></i> 3
                                        </div>
                                    </div>
                                </div>
                                <!--DETAILS-->
                                <div class="content">
                                    <h6>{{post.title.rendered}}</h6>
                                    <p v-html="post.excerpt.rendered"></p>
                                    <a class="forward" :href="post.link">Read More</a>
                                </div>
                            </div>
                        </div>
                    </div>
                    <div class="container">
                        <div class="row">
                            <div class="col text-center">
                                <button class="btn waves-effect" @click="loadMore()">Load More...</button>
                            </div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { wordpressAPI } from '../services/wordpressAPI'
import Loading from 'vue-loading-overlay';

const wordpress = new wordpressAPI();

export default {
    data(){
        return{
            isLoading: false,
            posts: [],
            isLoaded: [],
            media:[],
            page: 1,
        }
    },
    components: {
        Loading
    },
    created(){
        this.fetchPosts()
    },
    methods:{
        fetchPosts(){
            this.isLoading = true
            wordpress.getPosts(this.page).then((Data) => {
                Data.forEach(element => {
                    this.posts.push(element)
                });
                this.fetchMedia()
                this.isLoading = false
                //this.isLoaded = true
                // console.log(this.media)
            })
        },
        fetchMedia(){
            var count = 0;
            this.posts.forEach((post) => {
                // console.log(post.link)
                // this.isLoaded[count] = false
                this.$set(this.isLoaded, count, false)
                wordpress.getMedia(post.featured_media).then((Data) => {
                    // this.media[count]=Data
                    this.$set(this.media, count, Data)
                    console.log(post.featured_media+">"+this.media[0].id)
                    // this.isLoaded[count] = true
                    this.$set(this.isLoaded, count, true)
                    count++
                })
            })
            console.log(this.media)
            
        },
        isOdd(index){
            return index%2==0
        },
        getImage(index){
            if(this.isLoaded[index])
            return this.media[index].media_details.sizes.medium.source_url;
        },
        loadMore(){
            this.page += 1;
            this.fetchPosts()
            // console.log(this.isLoaded)
        }
    }
}
</script>
