import axios from 'axios'

const API_URL = "https://api.github.com/"

export class githubAPI{

    constructor(){

    }

    getrepos() {
        const url = API_URL+'users/danushka96/repos?sort=updated';
        return axios.get(url).then(response => response.data)
    }

    getrepo(repoid){
        const url = API_URL+'repos/danushka96/'+repoid;
        return axios.get(url).then(response => response.data)
    }

    getContributions(repoid){
        const url = API_URL+`repos/Danushka96/${repoid}/contributors`;
        return axios.get(url).then(response => response.data)
    }

}