<template>
    <div id="singleProject">
        <div class="back-button"><router-link to="/projects"><span><i class="fa fa-angle-double-left"></i></span></router-link></div>

        <!--==========================================
                        BACKGROUND
        ===========================================-->
        <div class="single-background"></div>

        <!--==========================================
                        SINGLE PORTFOLIO
        ===========================================-->
        <section class="single-page">
            <div class="container">
                <!--SECTION TITLE-->
                <div class="section-title">
                    <h4 class="text-uppercase text-center"><img src="images/icons/safe.png" alt="demo">{{this.tempProject.title}}</h4>
                </div>
                <!--PORTFOLIO HOLDER-->
                <div class="row" id="primary">
                    <!--PORTFOLIO CONTENT-->
                    <div id="content" class="col-sm-12">
                        <div class="post card">
                            <!--PORTFOLIO IMAGE SLIDER-->
                            <div class="post-image">

                                <!-- SLIDER STRUCTURE -->
                                <div id="clients-slider"
                                    class="swiper-container swiper-container-portfolios">
                                    <div class="swiper-wrapper" style="background: white">
                                        <!-- SLIDE ONE -->
                                        <hooper v-if="this.tempProject.screenshots.length>0" :itemsToShow="1.25" :centerMode="true" style="height:20%">
                                            <slide v-for="(screenshot,ss) in this.tempProject.screenshots" :key="ss">
                                                <div class="swiper-slide">
                                                    <img class="img-responsive" :src="screenshot">
                                                </div>
                                            </slide>
                                        </hooper>
                                    </div>
                                    <!-- ADD PAGINATION -->
                                    <div class="swiper-pagination swiper-pagination-portfolios"></div>
                                </div>

                            </div>

                            <!--PORTFOLIO TITLE AND DETAILS-->
                            <header>
                                <hr>
                                <div class="project-detail">
                                    <p><strong>Client :</strong>{{this.tempProject.client}}</p>
                                    <p><strong>Delivered :</strong>{{this.tempProject.delivery}}</p>
                                    <p><strong>Link :</strong><a :href="this.tempProject.source">{{this.tempProject.source}}</a></p>
                                    <p><strong>Demo :</strong><a :href="this.tempProject.demo">{{this.tempProject.demo}}</a></p>
                                    <p><strong>Type :</strong><span v-for="(type, index) in this.tempProject.type" :key="index"> {{type}}</span></p>
                                </div>
                            </header>
                            <!--BODY-->
                            <div class="post-body">
                                <h3>Project Details</h3>
                                <p>{{this.tempProject.description}}</p>
                            </div>
                        </div>

                    </div>

                </div>
            </div>
        </section>

    </div>
</template>

<script>
import { db } from '../config/firebase.config'
import { Hooper, Slide } from 'hooper';
import 'hooper/dist/hooper.css';

export default {
    name: 'project',
    components: {
        Hooper,
        Slide
    },
    data(){
        return {
            id: "",
            tempProject: {},
        }
    },
    mounted(){
        this.id = this.$route.params.id
        db.collection('projects').doc(`${this.id}`).get().then((querysnapshot) => {
            this.tempProject = querysnapshot.data();
        })
    }
}
</script>
