import axios from 'axios'

const POSTS_URL = "https://thesigma.gq/index.php/wp-json/wp/v2/posts?"
const AUTHOR = "author=3"
const PERPAGE = "&&per_page=3"
const PAGE = "&&page="
const MEDIA_URL = "https://thesigma.gq/index.php/wp-json/wp/v2/media/"

export class wordpressAPI{
    constructor(){

    }

    getPosts(page){
        const url = POSTS_URL+AUTHOR+PERPAGE+PAGE+page;
        return axios.get(url).then(respose => respose.data)
    }

    getMedia(id){
        const url = MEDIA_URL+id;
        return axios.get(url).then(respose => respose.data)
    }

}