<template>
    <section id="contact" class="section">
        <div class="container">
            <!-- SECTION TITLE -->
            <div class="section-title">
                <h4 class="text-uppercase text-center"><img src="images/icons/envelope.png" alt="demo">Add New Project</h4>
            </div>
            <div class="row">
                <div id="contact-card" class="col-md-12 col-sm-12 col-xs-12">
                    <!-- CONTACT FORM -->
                    <div class="card">
                        <div class="card-content">
                            <!-- <form id="contact-form" name="c-form"> -->
                                <!-- Title -->
                                <div class="input-field">
                                    <input id="title" type="text" class="validate" name="title" required v-model="project.title">
                                    <label for="title">Title</label>
                                </div>
                                <!--Client-->
                                <div class="input-field">
                                    <input id="client" type="text" class="validate" name="client" v-model="project.client"> 
                                    <label for="client">Client</label>
                                </div>
                                <!--source-->
                                <div class="input-field">
                                    <input id="source" type="text" class="validate" name="source" required v-model="project.source">
                                    <label for="source">source</label>
                                </div>
                                <!--Demo-->
                                <div class="input-field">
                                    <input id="demo" type="text" class="validate" name="demo" required v-model="project.demo">
                                    <label for="demo">Demo</label>
                                </div>
                                <!--description-->
                                <div class="input-field">
                                    <textarea id="description" class="materialize-textarea" name="description"
                                            required v-model="project.description"></textarea>
                                    <label for="description">Description</label>
                                </div>
                                <!-- Delivery Date -->
                                <div class="input-field">
                                    <input id="delivery" type="text" class="validate" name="delivery" required  v-model="project.delivery">
                                    <label for="delivery">Delivery Date</label>
                                </div>
                                <!-- Technologies -->
                                <div class="input-field">
                                    <input id="tech" type="text" class="validate" name="tech" required  v-model="technologies">
                                    <label for="tech">Technologies</label>
                                </div>
                                <!-- types -->
                                <div class="input-field">
                                    <input id="types" type="text" class="validate" name="types" required  v-model="types">
                                    <label for="types">Project Type</label>
                                </div>
                                <!--Image-->
                                <div class="input-field">
                                    <input id="image" type="text" class="validate" name="image" required  v-model="project.image">
                                    <label for="image">Image</label>
                                </div>
                                <!--Image-->
                                <div class="input-field">
                                    <input id="screenshot" type="text" class="validate" name="screenshot" required  v-model="screenshots">
                                    <label for="screenshot">screenshot</label>
                                </div>
                                <!-- SEND BUTTON -->
                                <div class="contact-send">
                                    <button id="submit" @click="saveProject()" name="contactSubmit" type="submit" value="Submit"
                                            class="btn waves-effect">Save
                                    </button>
                                </div>
                            <!-- </form> -->
                        </div>
                        <!--FORM LOADER-->
                        <div id="form-loader" class="progress is-hidden">
                            <div class="indeterminate"></div>
                        </div>
                    </div>
                </div>
            </div>
        </div>
    </section>
</template>

<script>
import { db } from '../config/firebase.config'

export default {
    name: "addProject",
    data() {
        return {
            project: {
                title: '',
                source: '',
                client: '',
                type: [],
                technologies: [],
                description: '',
                screenshots: [],
                demo: '',
                delivery: ''
            },
            technologies: "",
            types: "",
            screenshots: ""
        }
    },
    methods: {
        saveProject(){
            this.project.technologies = this.technologies.split(',')
            this.project.type = this.types.split(',')
            this.project.screenshots = this.screenshots.split(',')
            db.collection('projects').add(this.project).then(() => {
                //console.log(result)
                this.$router.push({path: '/projects'})
            });
        }
    }
}
</script>
