<script>
import VueRecaptcha from 'vue-recaptcha';
import { db } from '../config/firebase.config'
import BlogPosts from '../components/BlogPosts'

export default {
    data(){
        return {
            message: {
                name: "",
                subject: "",
                email: "",
                message: ""
            }
        }
    },
    components: { VueRecaptcha, BlogPosts },
    methods: {
        sendMessage(){
            db.collection('messages').add(this.message).then(() => {
                let notify = this.$toasted.show("Message Sent Successfully", {
                    theme: "toasted-primary", 
                    position: "bottom-left", 
                    duration : 5000,
                    icon : {
                        name : 'check',
                        after : true
                    }
                });
                this.message = {
                    name: "",
                    subject: "",
                    email: "",
                    message: ""
                };
            });
        }
    }
}
</script>
