<template>
    <div id="projectsPage">

        <!--==========================================
                        BACK TO HOME
        ===========================================-->
        <router-link to="/"><div class="back-button"><span><i class="fa fa-angle-double-left"></i></span></div></router-link>


        <!--==========================================
                        BACKGROUND
        ===========================================-->
        <div class="single-background"></div>

        <!--==========================================
                        SINGLE BLOG
        ===========================================-->
        <section class="single-page">

            <div class="container">

                <!--SECTION TITLE-->
                <div class="section-title">
                    <h4 class="text-uppercase text-center"><img src="images/icons/post-it.png" alt="demo">Projects</h4>
                </div>
                <!--BLOG HOLDER-->
                <div class="row" id="primary">
                    
                    <!--BLOG CONTENT-->
                    <div id="content" class="col-sm-4"  v-for="(project, index) in projects" :key="index">
                        
                        <loading :active.sync="isLoading" :can-cancel="true" ></loading>

                        <!--ARTICLE-->
                        <article class="post card">
                            <!--ARTICLE IMAGE-->
                            <div class="post-image">
                                <img :src="project.data.image" alt="demo">
                            </div>
                            <!--HEADER AND TAGS-->
                            <header>
                                <h2><router-link :to="'/project/'+project.id">{{project.data.title}}</router-link>
                                    <time datetime="2045-08-16">{{project.data.delivery}}</time>
                                </h2>
                                <hr>
                                <!-- post-details -->
                                <div class="post-details">
                                    <div class="chip" v-for="(technology, i) in project.data.technologies" :key="i">{{technology}}</div>
                                </div>
                            </header>
                            <!--ARTICLE BODY-->
                        </article>
                    </div>
                </div>
            </div>
        </section>

    </div>
</template>

<script>
import { db } from '../config/firebase.config'
import Loading from 'vue-loading-overlay';

export default {
    name: "projects",
    data(){
        return {
            isLoading: true,
            projects: []
        }
    },
    components: {
        Loading
    },
    mounted(){
        this.isLoading = true;
        db.collection('projects').get().then((querysnapshot) => {
            querysnapshot.forEach(doc => {
                const project = {
                    id: doc.id,
                    data: doc.data()
                }
                this.projects.push(project);
            })
            this.isLoading = false;
        })
        // console.log(this.projects)
    },
}
</script>

